.nav {
&__link {
  position: relative;
  color: inherit;
  text-decoration: none;

  &::after{
    content: '';
    display: block;
    position: absolute;
    bottom: -8px;
    height: 1px;
    background-color: $main-text-color;
    width: 100%;
    transition: transform 0.3s;
    transform: scale(0);
  }

  &:hover::after {
    transform: scale(1);
  }
}

&__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
}




